.header {
  width: 100%;
  height: 111px;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (min-width: 768px) {
    justify-content: left;
  }
  &__link {
    cursor: pointer;
    &__logo {
      width: 201px;
      @media (min-width: 768px) {
        margin-left: 40px;
      }
    }
  }
}
